@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
* {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
  background-color: #1a1a1c;
  color: #fff;
}

.logo {
  display: block;
  margin: 10px auto;
  width: 25%;
  height: 10%;
}

.char-search{
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.char-input{
  margin-top: 20px;
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 4px;
  border: none;
  background-image: linear-gradient(-225deg, #beb4b4 0%, #857c7c 48%, #534d4d 100%);
  color: #000;
}

.char-input:-ms-input-placeholder{
  color: #e2e2e2;
}

.char-input::placeholder{
  color: #e2e2e2;
}
.characters {
  /* margin: 20px;
  padding: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; */
  margin: 20px 10px;
  display: inline-block;
  justify-content: center;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
  border-radius: 5px;
}

.characters:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

.char-card {
  display: flex;
  flex-direction: row;
  border: 1px solid #d7d7d7;
  max-width: 400px;
}

.char {
    padding: 2px 16px;
}

.char h1{
    margin-top: 5px;
    font-size: 20px;
    width: 150px;
    color: #d7d7d7;
    font-weight: 700;
}

.description {
    font-size: 11px;
    display: block;
}

button {
    width: 70px;
    
}

button:hover {
    background-color: #d42121;
    cursor: pointer;
}
.modal {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
}

.modal-content {
    width: 500px;
    background-color: #fff;
}

.modal-header, .modal-footer {
    padding: 10px;
}

.modal-title {
    margin: 0;
}

.modal-body {
    padding: 10px;
    border-top: 1px solid #eee;
    border-bottom: 1px solid #eee;
}
